
@mixin font-xs() {
  font-family: $font-default;
  font-weight: $weight-light;
  font-size: $font-size-xxs;
  line-height: $font-size-xs;
  color: $gray-shade-1;
}
@mixin font-sm() {
  font-family: $font-default;
  font-weight: $weight-light;
  font-size: $font-size-xs;
  line-height: $line-height-xs;
  color: $gray-shade-1;
}
@mixin font-sm-dark() {
  font-family: $font-default;
  font-weight: $weight-light;
  font-size: $font-size-xs;
  line-height: $line-height-xs;
  color: $gray-shade-4;
}
@mixin font-md() {
  font-family: $font-default;
  font-weight: $weight-normal;
  font-size: $font-size-sm;
  line-height: $line-height-h5;
  color: $gray-shade-4;
}
@mixin font-xm() {
  font-family: $font-default;
  font-weight: $weight-semibold;
  font-size: $font-size-h6;
  line-height: $line-height-h6;
  color: $gray-shade-6;
}
@mixin font-lg() {
  font-family: $font-default;
  font-weight: $weight-light;
  font-size: $font-size-h4;
  line-height: $line-height-h4;
  color: $gray-shade-6;
}
@mixin font-xl() {
  font-family: $font-default;
  font-weight: $weight-light;
  font-size: $font-size-h3;
  line-height: $line-height-h3;
  color: $gray-shade-6;
}

@mixin shadow($shadowArgs...) {
  -moz-box-shadow: $shadowArgs;
  -webkit-box-shadow: $shadowArgs;
  box-shadow: $shadowArgs;
}

/* Box sizing */
@mixin box-sizing($box-model) {
  -webkit-box-sizing: $box-model; // Safari <= 5
  -moz-box-sizing: $box-model; // Firefox <= 19
  box-sizing: $box-model;
}

/* transitions */
@mixin transition($transition...) {
  -moz-transition:    $transition;
  -o-transition:      $transition;
  -webkit-transition: $transition;
  transition:         $transition;
}

/* gradients */
@mixin gradient2($direction, $color1, $color2) {
  background: -moz-linear-gradient($direction, $color1 0%, $color2 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient($direction, $color1 0%,$color2 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to $direction, $color1 0%,$color2 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

@mixin gradient3($direction, $color1, $color2, $color3) {
  background: -moz-linear-gradient($direction, $color1 0%, $color2 66%, $color3 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient($direction, $color1 0%,$color2 66%,$color3 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to $direction, $color1 0%,$color2 66%,$color3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

/* rounded corners */
@mixin rounded($radius: 0.5em) {
  border-radius: $radius;
}

@mixin border-corner-radius($corner, $size) {
  @if $corner == bl {
    border-bottom-left-radius: $size;
  }
  @else if $corner == br {
    border-bottom-right-radius: $size;
  }
  @else if $corner == tl {
    border-top-left-radius: $size;
  }
  @else {
    border-top-right-radius: $size;
  }
}

@mixin border-radius($baseSize) {
  @each $sizeHeader, $size in (xxs: .5, xs: 1, sm: 2, md: 3, lg: 4, xl: 5) {
    .radius-#{$sizeHeader} {
      @include rounded($size * $baseSize);
    }
    @each $corner in bl, br, tl, tr {
      .radius-#{$corner}-#{$sizeHeader} {
        @include border-corner-radius($corner, $size * $baseSize);
      }
    }
  }
}

/* transform */
@mixin transform($transform...) {
  transform:          $transform;
  -ms-transform:      $transform;
  -webkit-transform:  $transform;
  -o-transform:       $transform;
  -moz-transform:     $transform;
}

/* animation */
@mixin animate($name, $duration, $count: infinite, $direction: normal, $timing: ease) {
  -moz-animation-name: $name;
  -webkit-animation-name: $name;

  -moz-animation-duration: $duration;
  -webkit-animation-duration: $duration;

  animation-iteration-count: $count;
  -moz-animation-iteration-count: $count;
  -webkit-animation-iteration-count: $count;

  animation-direction: $direction;
  -moz-animation-direction: $direction;
  -webkit-animation-direction: $direction;

  -webkit-animation-timing-function: $timing;
  animation-timing-function: $timing;
}