@import 'styles/variables';
@import 'styles/mixins';
@import 'styles/buttons';


#upgrade-mfa {
  overflow: auto;
  background-color: #ffffff;
  width: 444px;

  .upgrade-mfa-icon {
    text-align: center;
    margin-top: 33px;
    margin-bottom: 24px;

    img {
      width: 147px;
    }
  }

  .upgrade-mfa-text  {
    font-family: $font-default;
    letter-spacing: normal;
    font-stretch: normal;
    font-style: normal;
    max-width: 380px;
    margin: 16px 32px 0 32px;

    .upgrade-mfa-title {
      font-size: $font-size-h4;
      font-weight: 500;
      line-height: 1.35;
      color: $gray-shade-4;
    }

    .upgrade-mfa-main-text {
      font-size: $font-size-sm;
      font-weight: $weight-normal;
      line-height: 1.71;
      color: $gray-shade-45;
      margin-top: 16px;
    }
  }

  .buttons {
    margin: 32px;
  }

  .pull-right {
    float: right;

    &:after {
      content: "";
      clear: both;
      display: table;
    }
  }
}