
@each $sizeHeader, $size in (xxs: 1, xs: 2, sm: 3, md: 4, lg: 5, xl: 6) {
  .btn-container-#{$sizeHeader} {
    min-width: $size * 30px;
    max-width: 1.25 * $size * 30px;
  }
}

.btn-base {
  overflow: hidden;
  cursor: pointer;
  background: $white;
  border: none;
  box-shadow: $shadow-z1;
  text-align: center;
  padding: 10px 10px;
  width: 100%;

  @include font-md();
  
  &.btn-primary {
    color: $white;
    background-color: $red;
    border-color: $red-shade-3;

    &:focus, &:hover, &:active {
      color: $white;
      background-color: $red-shade-3;
    }
    &:active {
      border-color: $red-shade-6;
    }
  }
  &.btn-blue {
    color: $white;
    background-color: $aqua;
    border-color: $aqua;

    &:focus, &:hover, &:active {
      color: $white;
      background-color: $blue-shade-3;
    }
  }
  &.btn-gray {
    color: $gray-shade-2;

    &:focus, &:hover, &:active {
      color: $gray-shade-5;
    }
  }
  &.btn-link {
    color: $aqua;
    box-shadow: none;
    background-color: $transparent;

    &:focus, &:hover, &:active {
      color: $aqua-shade-3;
    }
  }
  &.btn-small {
    font-size: $font-size-xs;
    line-height: $line-height-xs;
    padding: 10px 4px 9px 4px; //36px height
  }
  &.btn-transparent {
    padding: 0;
    text-align: left;
    margin: 0;
    text-transform: uppercase;
    background-color: $transparent;
    box-shadow: none;
    font-size: 11px;
    font-weight: 600;
    display: block;
  }
  &.btn-round {
    border-radius: 30px;
    width: initial;
    padding: 10px 25px;
    height: 36px;
    line-height: normal;
    font-size: 11px;
    text-transform: uppercase;
  }
}

.btn-link {
  text-align: center;
  border: none;
  cursor: pointer;
  color: $gray-shade-2;

  &:focus, &:hover {
    color: $gray-shade-5;
  }
  &:active {
    color: $gray-shade-5;
  }
}
.btn-aqua {
  color: $aqua;
}
.options-btn {
  cursor: pointer;
  border: none;
  padding: 4px 15px;

  &.more {
    background-color: $white;
    color: $gray-shade-4;
    position: relative;
    &:focus, &:hover {
      background-color: $aqua;
      color: $white;
    }
  }

  &.less {
    background-color: $aqua;
    color: $white;
    &:focus, &:hover {
      background-color: $aqua-shade-3;
    }
  }
}